/**
 */
export interface GeneratorTabs extends BaseAreaView<IRegPointer<IWspUiEnv>> {
	initialize(init: OGeneratorsInit): this
}


export interface OGeneratorTabsInit extends OGeneratorsInit {
	lastDatas?: JLDGeneratorTabs,
}

export class GeneratorTabs extends BaseAreaView<IRegPointer<IWspUiEnv>> implements IInfoConsumer, ILastDatasBuilder, IViewsContainer {
	reg: IReg<IWspUiEnv>;
	wsp: Wsp;

	/** Tab non figÃ©, qui suit l'item courant via l'infoBroker. */
	liveTab: GeneratorTab;

	/** Tab actuellement actif. */
	currentTab: GeneratorTab;

	genTabs: HTMLElement;
	gens: Generators;

	/** Uri de destinatiob libre. Usage : styler */
	customDestPath?: wspUri;

	protected pubNodeMatchFilter?: (pubNode: IItemPubNode) => boolean;

	protected _initialize(init: OGeneratorsInit) {
		super._initialize(init); //init this.reg, this.area, this.areaContext
		this.wsp = this.reg.env.wsp;
		const sr = this.attachShadow(DOMSH.SHADOWDOM_INIT);
		this._initAndInstallSkin(this.localName, init);
		//this.setAttribute("layout", (init.area as IWbToolLocatedArea)?.layout || 'h');
		this.pubNodeMatchFilter = init.pubNodeMatchFilter;

		this.genTabs = sr.appendChild(<c-arrow-scroll-box id="tabs"/>);
		this.genTabs.onkeydown = function (this: HTMLElement, ev: KeyboardEvent) {
			switch (ev.code) {
			case 'ArrowLeft' : {
				const genView = DOMSH.findHost<GeneratorTabs>(this);
				const tab = genView.currentTab.tab.previousElementSibling as IGeneratorTabPointer; //TODO RTL
				if (tab) tab.genTab.tab.genTab.itemPtr.focus();
				break;
			}
			case 'ArrowRight' : {
				const genView = DOMSH.findHost<GeneratorTabs>(this);
				const tab = genView.currentTab.tab.nextElementSibling as IGeneratorTabPointer;
				if (tab) tab.genTab.tab.genTab.itemPtr.focus();
				break;
			}
			default:
				return;
			}
			ev.stopPropagation();
			ev.preventDefault();
		};

		this.genTabs.ondragover = function (this: HTMLSpanElement, ev: DragEvent) {
			ev.preventDefault();
			ev.stopImmediatePropagation();
			const me = DOMSH.findHost<GeneratorTabs>(this);
			const shortDescs = ITEM.getShortDescsTransferFromDragSession(me.reg, ev.dataTransfer, {targetSrcType: "item"}, this);
			//on autorise le drop si item local
			ev.dataTransfer.dropEffect = !shortDescs || shortDescs.srcRefs.length < 1 || shortDescs.wspCd !== me.reg.env.wsp.code ? "none" : "link";
		};
		this.genTabs.ondrop = function (this: HTMLSpanElement, ev: DragEvent) {
			ev.preventDefault();
			ev.stopImmediatePropagation();
			const me = DOMSH.findHost<GeneratorTabs>(this);
			const shortDescs = ITEM.getShortDescsTransferFromDragSession(me.reg, ev.dataTransfer, {targetSrcType: "item"}, this);
			if (!shortDescs || shortDescs.isImport || shortDescs.srcRefs.length < 1 || shortDescs.wspCd !== me.reg.env.wsp.code) return;
			me.setCurrentTab(new GeneratorTab(me, false, {srcRef: shortDescs.srcRefs[0]}));
		};

		this.gens = sr.appendChild(new Generators().initialize(init)) as Generators;
		this.gens.onCodePubChange.add(function (gens: Generators) {
			const me = DOMSH.findHost<GeneratorTabs>(gens);
			if (me.currentTab) me.currentTab.codePub = me.gens.codePub;
		});

		if (this.reg.env.infoBroker) this.reg.env.infoBroker.addConsumer(this);
		this.reg.env.place.eventsMgr.on("onConnectionRenewed", () => {this.refreshStates()}); //on ne gÃ¨re pas le dÃ©sabonnement, considÃ©rant que la view est stable avec la place courante.
		this.liveTab = new GeneratorTab(this, true);
		this.genTabs.appendChild(<Button id="addTab" onclick={this.onAddTab} tabindex-custom="-1" icon="/@skin@/wsp/views/generators/addtab.svg" ui-context="bar"
																		 title="Ajoute un onglet pour un accÃ¨s stable aux publications d'un item"/>);
		if (init.lastDatas && init.lastDatas.tabs) {
			for (let i = 0, s = init.lastDatas.tabs.length; i < s; i++) {
				const t = new GeneratorTab(this, false, init.lastDatas.tabs[i]);
				if (init.lastDatas.currentTab === i) this.setCurrentTab(t);
			}
		}
		if (!this.currentTab) this.setCurrentTab(this.liveTab);
	}

	setCurrentTab(genTab: GeneratorTab) {
		if (this.currentTab) this.currentTab.setAsCurrentTab(false);
		this.currentTab = genTab;
		this.currentTab.setAsCurrentTab(true);
		this.gens.setSrc(genTab.itemPtr.shortDescs[0], genTab.codePub);
	}

	buildLastDatas?(parentLastDatas: JLastDatas): void {
		const lastDatasKey = this.getAttribute('last-datas');
		if (lastDatasKey) {
			let tab = DOM.findFirstChild(this.genTabs, isGeneratorTabPointer);
			if (this.liveTab === tab.genTab) tab = DOM.findNextSibling(tab, isGeneratorTabPointer);
			if (tab) {
				const ld = parentLastDatas[lastDatasKey] = {} as JLDGeneratorTabs;
				ld.tabs = [];
				do {
					const srcRef = tab.genTab.itemPtr.srcRef;
					if (srcRef) {
						if (this.currentTab === tab.genTab) ld.currentTab = ld.tabs.length;
						ld.tabs.push({
							srcRef: srcRef,
							codeGen: tab.genTab.codePub
						} as JLDGeneratorTab);
					}
				} while ((tab = DOM.findNextSibling(tab, isGeneratorTabPointer)));
			}
		}
	}

	async refreshStates() {
		await this.wsp.waitForAvailable(this);
		let tab = DOM.findFirstChild(this.genTabs, isGeneratorTabPointer);
		while (tab) {
			tab.genTab.itemPtr.fetchSrc();
			tab = DOM.findNextSibling(this.genTabs, isGeneratorTabPointer);
		}
	}

	async onAddTab(this: Button, ev: MouseEvent) {
		const me = DOMSH.findHost<GeneratorTabs>(this);
		const genTab = new GeneratorTab(me, false, {srcRef: null});
		genTab.genView.setCurrentTab(genTab);
		await SrcPointerFastSelect.SINGLETON.execute(genTab.itemPtr, ev);
		if (!genTab.itemPtr.srcRef) genTab.closeTab();
	}

	onInfo(info: IInfo): void {
		if (this.hidden) return;
		if (info instanceof InfoCurrentItem) this.liveTab.itemPtr.setSrcRef(info.srcUri, info.shortDesc);
	}

	onViewShown() {
		this.gens.onViewShown();
		this.liveTab.refreshLiveTab();
	}

	visitViews(visitor: (view: IViewApi) => any, options?: OViewVisitOptions): any {
		return visitor(this.gens);
	}

	visitViewsAsync(visitor: (view: IViewApi) => Promise<any>, options?: OViewVisitOptions): Promise<any> {
		return visitor(this.gens);
	}
}

