class GeneratorTab {
	tab: IGeneratorTabPointer;
	itemPtr: SrcPointerInline;
	btn: Button;
	codePub: string;
	pinned: boolean;

	constructor(public genView: GeneratorTabs, atFirst: boolean, lastDatas?: JLDGeneratorTab) {
		this.tab = genView.genTabs.insertBefore(<span class="genTab"/>, atFirst ? genView.genTabs.firstChild : null) as IGeneratorTabPointer;
		this.tab.genTab = this;
		this.itemPtr = this.tab.appendChild(new SrcPointerInline().initialize({
			reg: genView.reg,
			defaultAction: genView.reg.getSvc("ptrItemDblClick", SrcPointerDblClick.SINGLETON),
			sgnPattern: genView.reg.env.wsp.wspMetaUi.getGenOrCidSgnPattern(),
			showStatus: true,
			draggable: true,
			dropable: true,
			dispatchHighlight: true
		}));
		this.setPinState(lastDatas != null);
		this.itemPtr.classList.add("fixedIconSize");
		this.itemPtr.tabIndex = -1;
		this.itemPtr.addEventListener('focus', function (this: SrcPointerInline, ev: FocusEvent) {
			const genTab = (this.parentElement as IGeneratorTabPointer).genTab;
			genTab.genView.setCurrentTab(genTab);
		});
		this.itemPtr.onSrcRefChange.add(function (drawer: SrcDrawer) {
			const genTab = (drawer.parentElement as IGeneratorTabPointer).genTab;
			const src = drawer.shortDescs[0];
			if (!src || src.srcSt < 0) {
				if (genTab.pinned) genTab.closeTab();
				else drawer.setSrcRef(null); //item supprimÃ©, on efface le lien.
			}
			if (genTab.genView.currentTab === genTab) genTab.genView.gens.setSrc(src, genTab.codePub);
		});
		if (this.pinned) {
			this.codePub = lastDatas.codeGen;
			this.itemPtr.setSrcRef(lastDatas.srcRef);
			this.btn = this.tab.appendChild(this._newCloseButton("-1"));
		} else {
			this.btn = this.tab.appendChild(<Button class="dupTab" onclick={this.onPin} tabindex-custom="-1" icon="/@skin@/wsp/views/generators/duptab.svg" ui-context="bar"
																							title="Duplique cet onglet pour un accÃ¨s stable aux publications de l'item"/>) as Button;
			this.refreshLiveTab();
		}
	}

	setAsCurrentTab(current: boolean) {
		this.tab.classList.toggle('currentTab', current);
		this.itemPtr.tabIndex = current ? 0 : -1;
		this.btn.tabIndexCustom = current ? 0 : -1;
	}

	refreshLiveTab() {
		//on affecte l'item courant
		const req = new InfoReqCurrentItem();
		this.genView.reg.env.infoBroker.dispatchInfo(req, this);
		if (req.srcUri) this.itemPtr.setSrcRef(req.srcUri, req.shortDesc);
		else this.itemPtr.setSrcRef(null);
	}

	closeTab() {
		if (!this.pinned) return;
		if (this.genView.currentTab === this) this.genView.setCurrentTab((DOM.findNextSibling(this.tab, isGeneratorTabPointer) || DOM.findPreviousSibling(this.tab, isGeneratorTabPointer))?.genTab);
		this.tab.remove();
	}

	protected _newCloseButton(tabIndex: string): Button {
		return <Button onclick={this.onClose} tabindex-custom={tabIndex} icon="/@skin@/commons/icons/close.svg" ui-context="bar" title="Ferme la vue des publications de cet item"/> as Button;
	}

	protected onClose(this: Button) {
		(this.parentElement as IGeneratorTabPointer).genTab.closeTab();
	}

	protected onPin(this: Button, ev: MouseEvent) {
		const spanTab = this.parentElement as IGeneratorTabPointer;
		const genTab = spanTab.genTab;
		genTab.genView.liveTab = new GeneratorTab(genTab.genView, true);
		genTab.setPinState(true);
		//Remplace le bouton Pin par le bouton Close
		this.parentNode.insertBefore(genTab._newCloseButton(this.tabIndexCustom.toString()), this);
		this.remove();
		//on se replace aprÃ¨s le bouton addTab
		spanTab.parentNode.insertBefore(spanTab, DOM.findNextSibling(spanTab, isGeneratorTabPointer));
		genTab.genView.setCurrentTab(genTab);
		//if (genTab.itemPtr.srcRef == null) SrcPointerFastSelect.SINGLETON.execute(genTab.itemPtr, ev);
	}

	protected setPinState(pinned: boolean) {
		if (pinned) {
			this.pinned = true;
			this.itemPtr.readOnly = false;
			this.itemPtr.title = "";
		} else {
			this.pinned = false;
			this.itemPtr.readOnly = true;
			this.itemPtr.title = "Publications de l'item actif";
		}
	}
}

interface IGeneratorTabPointer extends HTMLElement {
	genTab: GeneratorTab
}

function isGeneratorTabPointer(n: any): n is IGeneratorTabPointer {return n?.genTab instanceof GeneratorTab}

interface JLDGeneratorTabs {
	tabs: JLDGeneratorTab[]
	currentTab: number
}

/** */
interface JLDGeneratorTab {
	srcRef: srcRef
	codeGen?: string
}


export interface Generators extends BaseAreaView<IRegPointer<IWspUiEnv>> {
	initialize(init?: OGeneratorsInit): this
}

export interface OGeneratorsInit extends OBaseAreaViewInit<IRegPointer<IWspUiEnv>> {

	/** Retourne true si le pubNode est conservÃ© */
	pubNodeMatchFilter?: (pubNode: IItemPubNode) => boolean

	/** Uri de destination libre. Usage : styler */
	customDestPath?: wspUri
}

export class Generators extends BaseAreaView<IRegPointer<IWspUiEnv>> implements ICidUiHandler {
	reg: IReg<IWspUiEnv>;
	wsp: Wsp;

	onCodePubChange = new EventMgr<(gen: Generators) => void>();

	codePub: string;

	pubTree: GridSmall;
	pubActions: BarActions<IPubCtx>;

	genRoot: HTMLElement;
	genRefreshBtn: Button;
	genIllus: HTMLImageElement;
	genInfos: HTMLElement;
	genSkins: HTMLFormElement;
	genProps: HTMLFormElement;

	cidRoot: HTMLElement;
	cidManifestHead: HTMLElement;
	cidDetails: HTMLElement; //select manifest, select process, userWait

	datas: GridDataHolderJsonTree<IItemPubNode> = new GridDataHolderJsonTree<IItemPubNode>('ch').setDefaultOpenState(defaultOpenStates);

	protected pubNodeMatchFilter?: (pubNode: IItemPubNode) => boolean;

	/** Uri de destination libre. Usage : styler */
	customDestPath?: wspUri;

	get shortDesc(): JSrcFields {return this.pubActions.actionContext.shortDesc}

	protected _initialize(init: OGeneratorsInit) {
		super._initialize(init); //init this.reg, this.area, this.areaContext
		this.wsp = this.reg.env.wsp;
		const sr = this.attachShadow(DOMSH.SHADOWDOM_INIT);
		this.reg.installSkin("scroll/small", sr);
		this.reg.installSkin("form-control-areas", sr);
		this._initAndInstallSkin(this.localName, init);
		const layoutV = (init.area as IWbToolLocatedArea)?.layout === 'v';
		this.reg.installSkin(layoutV ? "wsp-generators/v" : "wsp-generators/h", sr);

		this.pubNodeMatchFilter = init.pubNodeMatchFilter;
		this.customDestPath = init.customDestPath;

		this.pubTree = sr.appendChild(<GridSmall id="tree" Ã®={{
			reg: this.reg,
			columnDefs: [new GridColTreeDef('genTree')
				.setFlex('1rem', 1, 1).setMinWidth('55px').setChildrenIndent(1.5)
				.setCellBuilder(new CellBuilderGen())],
			hideHeaders: true,
			autoSelOnFocus: 'first',
			skinScroll: 'scroll/small',
			emptyBody: <div style="font-style:italic;">Aucune fonction de publication disponible</div>
		} as OGridSmallInit} c-resizable=""/>) as GridSmall;
		this.pubTree.dataHolder = this.datas;
		this.pubTree.addEventListener("grid-select", this.onGridSelect);
		sr.appendChild(<c-resizer c-orient={layoutV ? 'column' : 'row'}/>);
		const body = sr.appendChild(<section id="body" c-resizable=""/>);

		//GEN
		this.genRoot = body.appendChild(<div id="genRoot" hidden="true"/>);
		const genTop = this.genRoot.appendChild(<div id="genTop"/>);
		const genTopMain = genTop.appendChild(<div id="genTopMain"/>);
		this.genRefreshBtn = genTop.appendChild(<Button id="refresh" Ã®={{
			icon: "/@skin@/commons/icons/refresh.svg",
			uiContext: 'bar',
		} as OButtonInit} onclick={this.refreshGenRootUi} title="Rafraichir"/>) as Button;
		this.genIllus = genTopMain.appendChild(<div id="illusCtn"><img id="illus"/></div>).firstChild as HTMLImageElement;
		const topInfos = genTopMain.appendChild(<div id="topInfos"/>);
		this.genInfos = topInfos.appendChild(<div id="infos"/>);
		this.genSkins = topInfos.appendChild(<form id="skins"/>) as HTMLFormElement;
		this.genSkins.addEventListener("change", this.onSkinChange);
		this.genProps = this.genRoot.appendChild(<form id="props"/>) as HTMLFormElement;

		//CID
		this.cidRoot = body.appendChild(<div id="cidRoot" hidden="true"/>);
		this.cidManifestHead = this.cidRoot.appendChild(<div id="cidManifest"/>);
		this.cidDetails = this.cidRoot.appendChild(<div id="cidDetails"/>);

		this.pubActions = body.appendChild(<c-bar-actions Ã®={{
			reg: this.reg,
			actionContext: {
				reg: null, // this.reg non dynamique, propre Ã  chaque itemType
				uiContext: this,
				wsp: this.wsp,
				shortDesc: null,
				pubNode: null,
				genInfo: null,
				async genProps(): Promise<Jsonisable> {
					if ((this.uiContext as Generators).genRoot.hidden) return null;
					const skins = (this.uiContext as Generators).genSkins;
					const rootProps = (this.uiContext as Generators).genProps;
					if (skins.hidden && rootProps.hidden) return null;
					if (!rootProps.reportValidity()) return false;
					const props = {};
					if (!skins.hidden) FORMS.formToJson(skins, props);
					if (!rootProps.hidden) FORMS.formToJson(rootProps, props);
					return props;
				},
				customDestPath: this.customDestPath,
			} as IPubCtx,
			uiContext: "dialog",
			disableFullOverlay: true
		} as OBarActionsInit<IPubCtx>} id="actions" hidden="true"/>) as BarActions<IPubCtx>;

	}

	setSrc(shortDesc: JSrcFields, codeGenToSelect?: string) {
		const ctx = this.pubActions.actionContext;
		if (ctx.shortDesc === shortDesc) return; //Ã©vite refreshs successifs Ã  l'init (set currentTab, set ptrItem, msg init mainView).
		ctx.shortDesc = shortDesc;
		if (!shortDesc || shortDesc.srcSt < 0) {
			this.datas.setDatas([]);
		} else {
			const itemType = this.wsp.wspMetaUi.getItemType(shortDesc.itModel);
			const itemReg = ctx.reg = REG.createSubReg(this.reg, itemType);
			itemReg.env.securityCtx = SEC.createSub(this.reg.env.securityCtx, shortDesc.srcRoles, shortDesc.srcRi);
			const tree = itemType.buildPubTree(itemReg.env.securityCtx, this.pubNodeMatchFilter);
			this.datas.setDatas(tree);
			if (codeGenToSelect) {
				const newGen = itemType.getPub(codeGenToSelect, tree);
				if (newGen) {
					this.datas.openFolder(newGen); //Fore l'ouverture de la hiÃ©rerachie
					const offset = this.datas.getOffset(newGen);
					if (offset >= 0) this.pubTree.setSelectedRows(offset);
				}
			} else {
				const pub = tree[0];
				if (pub && (pub.genNodeType === "gen" || pub.genNodeType === "cid")) {
					//Si 1Ã¨re ligne n'est pas un dossier mais un gen, on le sÃ©lectionne par dÃ©faut
					this.pubTree.setSelectedRows(0);
				}
			}
		}
	}

	protected onGridSelect(this: GridSmall, ev: CustomEvent) {
		const generators = DOMSH.findHost<Generators>(this);
		const newPubNode = generators.datas.getDataByOffset(this.getSelectedRow());
		if (newPubNode) {
			generators.codePub = newPubNode.codeGenStack;
			generators.onCodePubChange.emit(generators);
		}
		if (newPubNode !== generators.pubActions.actionContext.pubNode) generators.setPubNode(newPubNode);
	}

	protected onSkinChange(this: HTMLFormElement, ev: CustomEvent) {
		//console.log("onSkinChange::::", ev);
		const generators = DOMSH.findHost<Generators>(this);
		generators.setGenIllus((ev.target as HTMLInputElement).value);
	}

	protected async setPubNode(pubNode: IItemPubNode | null) {
		if (pubNode) {
			if (pubNode.genNodeType === 'cid') {
				DOM.setHidden(this.cidRoot, false);
				DOM.setHidden(this.genRoot, true);
				return this.initViewCid(pubNode);
			} else if (pubNode.genNodeType !== 'dir') {
				DOM.setHidden(this.genRoot, false);
				DOM.setHidden(this.cidRoot, true);
				return this.initViewGen(pubNode);
			}
		}
		//Rien Ã  afficher...
		this.initViewEmpty();
	}

	protected initViewEmpty() {
		this.pubActions.actionContext.pubNode = null;
		this.pubActions.actionContext.genInfo = null;
		this.pubActions.actionContext.cidInfo = null;
		DOM.setHidden(this.genRoot, true);
		DOM.setHidden(this.cidRoot, true);
		DOM.setHidden(this.pubActions, true);
	}

	protected refreshGenRootUi(this: Button) {
		const generators = DOMSH.findHost<Generators>(this);
		generators.initViewGen(generators.pubActions.actionContext.pubNode);
	}

	protected async initViewGen(genNode: IItemPubNode) {
		try {
			const ctx = this.pubActions.actionContext;
			const idx = genNode.codeGenStack.lastIndexOf('_');
			const cdGen = idx < 0 ? genNode.codeGenStack : genNode.codeGenStack.substring(idx + 1);
			ctx.pubNode = genNode;
			const genInfoSvc: (wsp: Wsp, uiContext: HTMLElement, srcRef: srcRef, codeGenStack: string, addExtraInfos?: boolean, customFullUriDest?: string) => Promise<JGenInfo> = ctx.reg.getSvc("gen.action.genInfo") || ITEM.fetchGenInfo;
			const genInfo: JGenInfo = await genInfoSvc.call(this, this.wsp, this, SRC.srcRef(ctx.shortDesc), genNode.codeGenStack, genNode.needExtraProps, this.customDestPath);
			if (ctx.pubNode !== genNode) return; //race cond
			DOM.setHidden(this.genRoot, false);
			DOM.setHidden(this.genRefreshBtn, !genNode.showRefreshBtn || genInfo.status === "working");
			ctx.genInfo = genInfo;
			ctx.cidInfo = null;
			if (genInfo.skins && genInfo.skins.length > 1) {
				// Tri des skins par ordre alphabÃ©tique
				genInfo.skins.sort((skinA, skinB) => skinA.title.localeCompare(skinB.title, "en", {sensitivity: "base"}));
				DOM.setHidden(this.genSkins, false);
				//Skins
				render(this.skinsTpl(ctx), this.genSkins);
				this.genSkins["skin"].value = genInfo.skin || 'default'; //affectation du skin courant
			} else {
				DOM.setHidden(this.genSkins, true);
			}
			//Illus
			this.setGenIllus(genInfo.skin);
			//Infos
			render(await (genNode.infos || this.infosTpl)(ctx), this.genInfos);
			//Props
			const propsLists: string[] = [];
			propsLists.push(`params:gen:${cdGen}`);
			if (genNode.codeGenStack != cdGen) propsLists.push(`params:gen:${genNode.codeGenStack}`);
			propsLists.push(`params:genNature:${genNode.genNature}`);
			propsLists.push("params:gen");
			const propsListsAreas = ctx.reg.mergeLists<IArea<IPubCtx & IControlAreaContext> | Promise<IArea<IPubCtx & IControlAreaContext>>>(...propsLists);
			if (genNode.props || propsListsAreas) {
				DOM.setHidden(this.genProps, false);

				let tpl = genNode.props ? await genNode.props(ctx) : xhtml`<fieldset>
					<div class="fields" area-ids="*"/>
				</fieldset>`;

				// RÃ©initialisation nÃ©cessaire des components dont la structure HTML est constante,
				// tout en exploitant le contexte Ã  l'initialisation (reg, ...) : lit ne sait pas gÃ©rer ces cas
				// exemple : Ã©volution du contexte de 'DynGenParam' faux sans Ã§a lorsqu'on change d'item
				if (this.genProps.hasChildNodes()) render(null, this.genProps);
				render(tpl, this.genProps, {});

				let areaCtx: IPubCtx & IControlAreaContext = Object.create(ctx);
				areaCtx.buildControlLabel = true;
				if (propsListsAreas) await AREAS.applyLayout(this.genProps, await Promise.all(propsListsAreas), areaCtx, true);

				FORMS.jsonToForm(ctx.genInfo.storedProps || {}, this.genProps, false, false);//forceClear=false pour prÃ©server les valeurs par dÃ©faut
				DOM.setAttrBool(this.genProps, "disabled", genInfo.status === "working");
			} else {
				DOM.setHidden(this.genProps, true);
			}
			//Actions
			if (genInfo.status === 'working') {
				DOM.setHidden(this.pubActions, true);
				setTimeout(() => {
					if (ctx.pubNode === genNode) this.initViewGen(genNode);
				}, 10000); //sÃ©curise un refresh toutes les 10s en plus de websocket
			} else {
				DOM.setHidden(this.pubActions, false);
				const actionsLists: string[] = [];
				actionsLists.push(`actions:gen:${cdGen}`);
				if (genNode.codeGenStack != cdGen) actionsLists.push(`actions:gen:${genNode.codeGenStack}`);
				actionsLists.push(`actions:genNature:${genNode.genNature}`);
				actionsLists.push("actions:gen");
				if (genNode.actionListsCode) actionsLists.push(...genNode.actionListsCode);
				this.pubActions.actions = await Promise.all(ctx.reg.mergeLists(...actionsLists)) as IAction<IPubCtx>[];
			}
		} catch (e) {
			console.error(e);
			this.initViewEmpty();
		}
	}

	protected async initViewCid(cidNode: IItemPubNode) {
		try {
			const ctx = this.pubActions.actionContext;
			ctx.pubNode = cidNode;
			const allHosts = await CID.getCidHostsForOutput(this.reg);
			ctx.genInfo = null;
			ctx.cidInfo = {
				cidHosts: cidNode.cidHostIs === null ? [] : (cidNode.cidHostIs ? Array.from(CID.filterIsOr(allHosts, cidNode.cidHostIs)) : allHosts)
			};
			//TODO fetch le lastCidEndPoint pour ce src et ce cidNode.codeGenStack
			// pour afficher ces infos, dans la meme logique que
			// 		render(await (genNode.infos || this.infosTpl)(ctx), this.genInfos);

			if (ctx.pubNode !== cidNode) return; //race cond
			DOM.setHidden(this.cidRoot, false);
			//Fixation des donnÃ©es connues
			if (cidNode.cidManifestUrlInput || ctx.cidInfo.cidHosts.length !== 1) {
				//Interface de sÃ©lection du serveur
				this.cidSeletHost();
			} else {
				// 1 seul serveur connu
				ctx.cidInfo.lastManifest = await ctx.cidInfo.cidHosts[0].fetchManifest();
				if (ctx.pubNode !== cidNode) return; //race cond
				this.cidOnManifestKnown();
			}
		} catch (e) {
			console.error(e);
			this.initViewEmpty();
		}
	}

	/** Affiche l'interface de sÃ©lection d'un server Cid. */
	protected cidSeletHost() {
		const ctx = this.pubActions.actionContext;
		DOM.setHidden(this.pubActions, true);
		this.pubActions.actions = null;
		this.cidDetails.textContent = null;
		if (ctx.cidInfo.cidHosts.length > 0) {
			this.cidManifestHead.textContent = "SÃ©lection du serveur cible";
			const group = this.cidDetails.appendChild(<ul/>);
			for (let i = 0, s = ctx.cidInfo.cidHosts.length; i < s; i++) {
				const host = ctx.cidInfo.cidHosts[i];
				group.appendChild(<li><a onclick={this.onCidSelectHost} href={i}>{host.label}</a></li>);
			}
			if (ctx.pubNode.cidManifestUrlInput) this.buildInputManifest(group.appendChild(<li/>), "Autre");
		} else {
			this.cidManifestHead.textContent = "DÃ©finition du serveur cible";
			this.buildInputManifest(this.cidDetails.appendChild(<div id="manifestAlone"/>), "URL du serveur");
		}
	}

	buildInputManifest(parent: HTMLElement, label: string) {
		parent.appendChild(<div class="manifestLabel">{label}</div>);
		parent.appendChild(<div id="manifestEdit">
			<input id="manifestInput" type="url" oninput={this.onManifestInput} onchange={function (this: HTMLInputElement) {DOMSH.findHost<Generators>(this).onManifestChange(this)}}/>
			<c-button id="manifestValid" Ã®={{reg: this.reg, label: "Valider", disabled: true}} onclick={function (this: HTMLInputElement & ICidManifestPointer) {DOMSH.findHost<Generators>(this).cidUseManifest(this.cidManifest)}}/>
		</div>);
		parent.appendChild(<c-msg id="manifestInputResult" Ã®={{noShadow: true}}/>);
	}

	async onManifestChange(input: HTMLInputElement) {
		const msg = this.shadowRoot.getElementById("manifestInputResult") as MsgLabel;
		const btn = this.shadowRoot.getElementById("manifestValid") as Button & ICidManifestPointer;
		btn.cidManifest = null;
		if (!input.validity.valid) {
			msg.setCustomMsg("Saisissez une URL valide permettant d'obtenir les caractÃ©ristiques du serveur (manifeste CID)", "info");
			btn.cidManifest = null;
			btn.disabled = true;
		} else {
			const ctx = this.pubActions.actionContext;
			const pubNode = ctx.pubNode;
			try {
				btn.cidManifest = await CID.getCidManifest(input.value);
				if (pubNode !== ctx.pubNode) return; //race cond
			} catch (e) {
				console.log(e);
			}
			if (btn.cidManifest) {
				msg.setCustomMsg("Serveur reconnu : " + CID.getCidManifestLabel(this.pubActions.actionContext.cidInfo.cidHosts, btn.cidManifest), "info");
				btn.disabled = false;
			} else {
				msg.setCustomMsg("Aucun serveur reconnu Ã  cette URL (manifeste CID non trouvÃ©), ou politique de sÃ©curitÃ© du serveur incompatible", "error");
				btn.disabled = true;
			}
		}
	}

	onManifestInput(this: HTMLInputElement, ev: Event) {
		const me = DOMSH.findHost<Generators>(this);
		if (me._cidMnInputWaiter) clearTimeout(me._cidMnInputWaiter);
		me._cidMnInputWaiter = setTimeout(() => me.onManifestChange(this), 800) as any;
	}

	protected _cidMnInputWaiter: number;

	cidUseManifest(manifest: CidManifest) {
		if (!manifest) return;
		const ctx = this.pubActions.actionContext;
		ctx.cidInfo.lastManifest = manifest;
		this.cidOnManifestKnown();
	}

	/** */
	protected async onCidSelectHost(this: HTMLAnchorElement, ev: MouseEvent) {
		ev.preventDefault();
		const me = DOMSH.findHost<Generators>(this);
		const ctx = me.pubActions.actionContext;
		const cidNode = ctx.pubNode;
		ctx.cidInfo.lastManifest = await ctx.cidInfo.cidHosts[parseInt(this.getAttribute('href'))].fetchManifest();
		if (ctx.pubNode !== cidNode) return; //race cond
		me.cidOnManifestKnown();
	}

	/** ctx.cidInfo.lastManifest renseignÃ© */
	protected async cidOnManifestKnown() {
		const ctx = this.pubActions.actionContext;
		const cidNode: IDatasItemCidNode = ctx.pubNode;
		const manifest = ctx.cidInfo.lastManifest;
		if (!manifest) return this.cidSeletHost();
		this.cidManifestHead.textContent = "Serveur : " + CID.getCidManifestLabel(this.pubActions.actionContext.cidInfo.cidHosts, manifest);
		if (ctx.pubNode.cidManifestUrlInput || ctx.cidInfo.cidHosts.length !== 1) {
			//PossibilitÃ© de changer
			this.cidManifestHead.appendChild(<c-button class="changeBtn" icon="/@skin@/wsp/views/generators/change.svg" title="Changer de serveur"
																								 onclick={function (this: Button, ev: MouseEvent) {return DOMSH.findHost<Generators>(this).cidSeletHost()}}/>);
		}
		if (cidNode.cidProcessSelector) {
			ctx.cidInfo.cidProcesses = await cidNode.cidProcessSelector(ctx);
			if (cidNode !== ctx.pubNode) return; //race cond
		} else if (cidNode.cidProcessIs) {
			ctx.cidInfo.cidProcesses = manifest.getProcesses(...cidNode.cidProcessIs);
		}
		DOM.setHidden(this.pubActions, false);
		this.pubActions.actions = await Promise.all(ctx.reg.mergeLists(...(cidNode.actionListsCode || []), `actions:cid:${ctx.pubNode.codeGenStack}`, ctx.pubNode.codeGenStack.indexOf('_') > 0 ? "actions:cidGen" : "actions:cidScar")) as IAction<IPubCtx>[];
		this.cidRefreshOnSessionChange();
	}

	cidRefreshOnSessionChange() {
		const ctx = this.pubActions.actionContext;
		if (!ctx.cidInfo) return;
		this.pubActions.refreshContent();
		if (ctx.cidInfo.cidSession) {
			//console.log("cidRefreshOnSessionChange::::", ctx.cidInfo.cidSession);
			switch (ctx.cidInfo.cidSession.state) {
			case ECidSessionState.authenticating:
				this.cidDetails.textContent = null;
				this.cidDetails.appendChild(<div class="cidUploading"><span>Authentification en cours...</span></div>);
				return;
			case ECidSessionState.inStep:
			case ECidSessionState.uiProcessSel: //en principe impossible
			case ECidSessionState.notStarted: //en principe impossible
				this.cidDetails.textContent = null;
				this.cidDetails.appendChild(<div class="cidUploading"><span>DÃ©ploiement en cours...</span></div>);
				return;
			case ECidSessionState.uiAuth:
				// uiWait en cours
				return;
			case ECidSessionState.failed:
			case ECidSessionState.ended:
			case ECidSessionState.aborted:
				//on rÃ©affiche l'Ã©tat de la derniÃ¨re diffusion rÃ©ussie.
			}
		}
		this.cidDetails.textContent = null;
		//TODO info de la mÃ©moire derniÃ¨re diffusion
	}


	protected setGenIllus(skinCode: string) {
		const genInfo = this.pubActions.actionContext.genInfo;
		const genNode = this.pubActions.actionContext.pubNode;
		const skin = skinCode && genInfo && genInfo.skins ? genInfo.skins.find((s) => s.code === skinCode) : null;
		const src = skin && skin.hasIllus ? ITEM.getGenSkinIllusUrl(this.wsp, genNode, skin) : genNode.illusUrl;
		if (src !== this.prevIllusSrc) this.genIllus.src = this.prevIllusSrc = src;
	}

	protected prevIllusSrc: string;

	onViewHidden(closed?: boolean): void {
		if (closed) {
			this.reg.env.place.eventsMgr.removeListener("onConnectionRenewed", this._onConnectionRenewed);
			this.reg.env.universe.wsFrames.ws.msgListeners.removeListener("wspGen", this._onMsgWspGen);
			this._onMsgWspGen = null;
			this._onConnectionRenewed = null;
		}
	}

	onViewShown() {
		if (!this._onMsgWspGen) {
			//console.log("Generators: Init Ws listening..............");
			this._onMsgWspGen = (m: JWspGenMsg) => {
				const ctx = this.pubActions.actionContext;
				if (!ctx.pubNode || ctx.shortDesc.srcUri !== m.srcUri || m.wsp !== ctx.wsp.code || m.customDestPath !== this.customDestPath) return;
				this.setPubNode(ctx.pubNode);
			};
			this._onConnectionRenewed = () => {
				const ctx = this.pubActions.actionContext;
				if (ctx.pubNode) this.setPubNode(ctx.pubNode);
			}
			this.reg.env.place.eventsMgr.on("onConnectionRenewed", this._onConnectionRenewed);
			this.reg.env.universe.wsFrames.ws.msgListeners.on("wspGen", this._onMsgWspGen);
		}
	}

	protected _onConnectionRenewed: (states: JWspWorkingInitStates) => void;

	protected _onMsgWspGen: (m: JWspGenMsg) => void;

	infosTpl(ctx: IPubCtx): TemplateResult {
		let dt: string;
		const deploy = ctx.pubNode.genNature === 'deploy';
		if (ctx.genInfo.status === 'working') {
			dt = deploy ? "Transfert en cours..." : "GÃ©nÃ©ration en cours...";
		} else if (ctx.genInfo.lastGen) {
			const lastGenDate = DTFormatter.format(ctx.genInfo.lastGen);
			if (ctx.genInfo.status === 'failed') dt = deploy ? `Transfert en erreur le ${lastGenDate}` : `GÃ©nÃ©rÃ© en erreur le ${lastGenDate}`;
			else if (ctx.genInfo.status === 'warning') dt = deploy ? `TransfÃ©rÃ© avec anomalies le ${lastGenDate}` : `GÃ©nÃ©rÃ© avec anomalies le ${lastGenDate}`;
			else dt = deploy ? `TransfÃ©rÃ© le ${lastGenDate}` : `GÃ©nÃ©rÃ© le ${lastGenDate}`;
		} else if (ctx.genInfo.status === "failed") {
			dt = deploy ? "Dernier transfert en erreur" : "DerniÃ¨re gÃ©nÃ©ration en erreur";
		} else {
			dt = deploy ? "Non transfÃ©rÃ©" : "Non gÃ©nÃ©rÃ©";
		}
		return xhtml`<div class="genTi ${ctx.genInfo.status}">${ctx.pubNode.genTitle || ctx.genInfo.title}</div>
<div class="reason ${ctx.genInfo.reason ? '' : 'hidden'}">Un fichier de la gÃ©nÃ©ration est verrouillÃ©. FERMEZ les applications tierces et recommencez.</div>
<div class="dt ${ctx.genInfo.status}">${dt}</div>`;
	}

	skinsTpl(ctx: IPubCtx): TemplateResult {
		return xhtml`${ctx.genInfo.skins.map((skin: JGenSkin) => {
			return xhtml`<span class="skin"><input type="radio" name="skin" value="${skin.code}" id="sk_${skin.code}"/><label for="sk_${skin.code}">${skin.title}</label></span>`
		})}`
	}

	//*** ICidUiHandler ***

	async webAuth(session: CidSession, step: CidWebAuthStep): Promise<ICidStepResult> {
		const frame = <iframe style="flex:1; border: none;"/> as HTMLIFrameElement;
		if (UiThemeDeskFeat.isIn(desk)) (frame as any).injectTheme = UiThemeDeskFeat.injectTheme;
		const dialog = POPUP.showDialog<'ok' | null>(frame, this, {titleBar: "Authentification", initHeight: "min(60vh,40em)", initWidth: "60vw"});
		dialog.onNextClose().then((r) => {if (!r) session.onCidFrameMessage({cidAuth: "failed"})});
		const res = await step.initFrame(frame, session);
		dialog.close('ok');
		return res;
	}

	async uiWait(session: CidSession, step: CidStep): Promise<ICidStepResult | null> {
		this.cidDetails.textContent = null;
		const iframe = this.cidDetails.appendChild(<iframe class="cidWaiting"/>) as HTMLIFrameElement;
		if (UiThemeDeskFeat.isIn(desk)) (iframe as any).injectTheme = UiThemeDeskFeat.injectTheme;
		return step.initFrame(iframe, session);
	}

	async interact(session: CidSession, step: CidStep): Promise<ICidStepResult> {
		/** Ignorer les steps d'interaction optionnelles **/
		const ctx = this.pubActions.actionContext;
		if (session.config.cidPassOptionalInteraction && step.def.getAttribute("required") === "false") return {};
		const frame = <iframe style="flex:1; border: none;"/> as HTMLIFrameElement;
		if (UiThemeDeskFeat.isIn(desk)) (frame as any).injectTheme = UiThemeDeskFeat.injectTheme;
		const dialog = POPUP.showDialog<'ok' | null>(frame, this, {initHeight: "min(90vh,50em)", initWidth: "min(90vw,80em)", titleBar: {barLabel: {label: session.cidProcess.label.getStr("fr")}}, resizer: {}});
		dialog.onNextClose().then((r) => {if (!r) session.onCidFrameMessage({cidInteraction: "aborted"})});
		const res = await step.initFrame(frame, session);
		dialog.close('ok');
		return res;
	}

	async selectProcess(session: CidSession, choices: Set<CidProcess>): Promise<CidProcess | null> {
		//console.trace("TODO select Process UI");
		return choices.values().next().value;
	}
}

