/** */
class CellBuilderGen extends CellBuilderIconLabel {

	constructor() {
		super("label");
		this.iconKey = "iconUrl";
		this.iconFilter = "var(--filter)";
	}

	redrawCell(row: GridDataRowJsonTree<IItemPubNode>, root: HTMLElement): void {
		super.redrawCell(row, root);
		const span = root.firstElementChild as HTMLElement;
		if (row.rowDatas.genNodeType === 'dir') {
			span.style.display = row.rowDatas.iconUrl ? '' : 'none';
			root.style.fontWeight = 'bold';
		} else {
			span.style.display = '';
			root.style.fontWeight = 'normal';
		}
	}
}

/** */

/** API Ã  implÃ©menter par les pages fournies par le dynGenParam */
interface IWindowDynGenParam {
	_genPropsMgr: IWindowDynGenParamMgr
}

interface IWindowDynGenParamMgr {
	/**
	 * Initialise ou rafraichit le contexte du genBox
	 *	ATTENTION : cette mÃ©thode peut Ãªtre rÃ©-appelÃ©e aprÃ©s premiÃ¨re exploitation des props (ex : aprÃ©s une gÃ©nÃ©ration). Elle ne doit
	 * donc pas nÃ©cessairement reseter la page web (contexte dÃ©pendant)
	 * @param propsObj : propriÃ©tÃ©s ENREGISTREES uniquement
	 */
	refresh(propsObj: Jsonisable, isReadOnly: boolean, forceRefresh: boolean): void,

	/** compute l'objet des propriÃ©tÃ©s du gÃ©nÃ©rateur */
	get(): Jsonisable,

	/** contrÃ´le la validitÃ© du composant */
	checkValidity(): boolean,
}


export interface ODynGenParamInit extends ODynGenInit, OFormElementInit {
	reg?: IReg<IWspUiEnv>
	/**  */
	dynGenInit: ODynGenInit
}

export class DynGenParam extends MxFormElement(BaseElement) implements IViewContainer, IFormJsonisable {

	protected config: ODynGenParamInit;

	reg: IReg<IWspUiEnv>;

	view: DynGen;

	get viewGenPropsMgr(): IWindowDynGenParamMgr | null {
		if (this.view && this.view.genFrame && this.view.genFrame.contentWindow)
			return (this.view.genFrame.contentWindow as IWindowDynGen & IWindowDynGenParam)._genPropsMgr;
		return null;
	}

	_initialize(init: ODynGenParamInit) {
		this.config = init;
		this.reg = this.findReg(init);

		this.view = new DynGen().initialize(Object.assign({
			reg: this.reg,
			w: this.reg.env.wsp.code,
			autoResize: true,
		} as ODynGenInit, this.config));

		this.view.tabIndex = -1;

		this.view.genFrame.addEventListener("load", () => {
			this._refreshUi();
		});

		this._attach(this.localName, init, this.view);
		this._initializeForm(init);
		//this.refresh();
	}

	protected _refreshUi(forceDisabled?: boolean) {
		if (this.viewGenPropsMgr)
			this.viewGenPropsMgr.refresh(this._lastProps, forceDisabled ? true : this.disabled, false);
		this._refreshValidity()
	}


	visitViews(visitor: (view: IView) => any): any {
		if (this.view) return visitor(this.view);
	}

	visitViewsAsync(visitor: (view: IView) => Promise<any>, options?: OViewVisitOptions): Promise<any> {
		return this.view ? visitor(this.view) : Promise.resolve(undefined);
	}

	fillJson(parent: any, root: any): void {
		Object.assign(parent, this.viewGenPropsMgr?.get() || {});
	}

	protected _lastProps: Dict<any> = null

	extractJson(parent: Dict<any>): boolean {
		this._lastProps = parent;
		this.view?.load();
		return true;
	}

	/** Form */
	formDisabledCallback(disabled: boolean) {
		this._refreshUi(disabled);
	}

	protected _refreshValidity() {
		let validity = this.viewGenPropsMgr?.checkValidity();
		this.setValidity({badInput: !validity}, validity ? "" : "Valeur incorrecte", this.view);
	}

}

